@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");

$primary-color: #5b20be;

@mixin Flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin Margin {
  margin: 2rem 7.5rem;
}
@mixin MarginPhone {
  margin: 1rem 1rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen";
  background-color: #f9f9f9;
}

.Themes_container {
  .themes {
    @include Margin();
    .heading {
      font-size: 3rem;
      color: $primary-color;
      margin: 5rem 0;
      line-height: normal;
    }

    .theme_container {
      margin-bottom: 10rem;
      .theme_content {
        @include Flex();
        background-color: white;
        border-radius: 30px;
        box-shadow: 0 6px 11px -5px rgb(118, 118, 118);
        height: 250px;
        margin: 4rem 0;
        .left {
          //   border-radius: 30px;
          border-top-left-radius: 30px;
          border-bottom-left-radius: 30px;
          width: 500px;
          height: 100%;
          background-color: #d9d9d9;
        }
        .right {
          width: 100%;
          //   height: 100%;
          padding: 2.5rem;
          h2 {
            margin: 0;
            color: #0ead69;
            line-height: normal;
            font-size: 2rem;
            font-weight: 700;
          }
          p {
            font-size: 1.2rem;
            font-weight: 600;
            margin: 1.5rem 0;
            text-align: justify;
            // line-height: normal;
          }
        }
      }
    }
  }
}
