@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");

$primary-color: #5b20be;

@mixin Flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin Margin {
  margin: 2rem 7.5rem;
}
@mixin MarginPhone {
  margin: 1rem 1rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen";
  background-color: #f9f9f9;
}

.OrgComittee {
  h2 {
    // color: $primary-color;
    font-size: 3rem;
    margin: 7rem 0;
    text-align: center;
  }
  #heading {
    color: $primary-color;
  }
}

.member {
  @include Flex();
  flex-direction: column;
  img {
    width: 170px;
    height: 170px;
  }

  .por {
    font-size: 1.7rem;
    font-weight: 700;
    margin: 5px;
  }
  .detail {
    color: #767676;
    font-size: 1.1rem;
  }
}

.vip {
  margin: 7rem 0;
}

.member_container {
  @include Flex();
  margin: 0 9rem 9rem 9rem;
  //   gap: 10rem;
  row-gap: 6rem;
  column-gap: 9rem;
  flex-wrap: wrap;
}
