$breakpoints: (
  "phone": 390px,
  "tablet": 768px,
) !default;

@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");

$primary-color: #5b20be;

@mixin Flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin Margin {
  margin: 2rem 7.5rem;
}
@mixin MarginPhone {
  margin: 1rem 1rem;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen";
  background-color: #f9f9f9;

  @media (max-width: map-get($breakpoints, "phone")) {
    font-size: 0.9rem;
  }
}

//*************************** Navbar ********************************

.Navbar {
  .container {
    @include Flex;
    @include Margin;
    justify-content: space-between;

    .logo_container {
      padding: 0.8rem 3rem;
      border-radius: 7px;
      border: solid 2px $primary-color;
    }

    .btn {
      background-color: $primary-color;
      padding: 0.8rem 3rem;
      color: white;
      font-size: 1.2rem;
      border-radius: 7px;
    }

    @media (max-width: map-get($breakpoints, "phone")) {
      @include MarginPhone();
    }
  }
}

//*************************** Menubar ********************************

.Menubar {
  .container {
    @include Margin();
    @include Flex();
    list-style-type: none;
    justify-content: space-around;
    height: 4rem;
    padding: 0;
    // font-size: 1.2rem;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 2px 8px rgba(126, 143, 211, 0.06);

    @media (max-width: map-get($breakpoints, "phone")) {
      // @include MarginPhone();
      margin: 1rem 0;
      padding: 1rem 0;
      .list-items {
        font-weight: 400;
      }
    }

    .list-items {
      @include Flex();
      font-weight: 600;
      text-align: center;
      padding: 0.8rem 0.5rem;
      width: 11rem;
      cursor: pointer;
      color: #858585;
      border-bottom: solid 3px white;
    }
    .list-items:hover {
      border-bottom: solid 3px $primary-color;
      color: black;
      transition: 0.25s ease-in;
    }
  }
}

//*************************** Slider ********************************

.slider_container {
  // @include Margin();
  margin: 0 0 3rem 0;
  .elipse {
    z-index: 10;
    position: absolute;
    background: #f9f9f9;
    height: 80px;
    width: 98.8vw;
  }

  @media (max-width: map-get($breakpoints, "phone")) {
    // height: 100px;
    .elipse {
      // z-index: 10;
      // position: absolute;
      // background: #f9f9f9;
      height: 40px;
      width: 98.8vw;
    }

    .slide {
      background: #f9f9f9;
      height: 100px;
      img {
        object-fit: contain;
      }
    }
    .swiper {
      // position: absolute;
      transform: translate(0, -30px);
      // transform: translate(0, 40px);
      width: 100%;
      height: 100px;
    }
  }

  #upper_elipse {
    border-radius: 50% / 0 0 100% 100%;
  }
  #lower_elipse {
    position: absolute;
    border-radius: 50% / 100% 100% 0 0;
    transform: translate(0, -135px);
  }

  .slide {
    background: #f9f9f9;
    height: 540px;
    img {
      object-fit: contain;
    }
  }
  .swiper {
    // position: absolute;
    transform: translate(0, -30px);
    // transform: translate(0, 40px);
    width: 100%;
    height: 600px;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #f9f9f9;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

//*************************** Link Card ********************************

.main_container {
  @include Margin();
  display: grid;
  margin-top: 3rem;

  @media (max-width: map-get($breakpoints, "phone")) {
    @include MarginPhone();
    display: inline-block;
    .card_container {
      // height: 410px;

      .quick_link {
        display: none;
        // grid-column: 1;
        width: 18vw;
        font-size: 1.2rem;
        position: sticky;
        top: 100px;

        #quick_link {
          color: white;
          background-color: #66be94;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
          padding: 1rem 3rem 1rem 3rem;
        }

        .link {
          list-style: none;
          a {
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: black;
            background-color: white;
            padding: 0.8rem 2rem;
            text-decoration: none;
            margin: 7px 0;

            // &:hover {
            // }
          }
          #register {
            color: white;
            background-color: $primary-color;
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }
        }
      }
    }

    .right_section {
      // grid-column: 2;
      padding: 0;
      margin: 0;
      border-radius: 1.5%;
      background-color: white;
      .content {
        margin: 3rem 0;

        h1 {
          margin: 0;
          padding: 0;
          font-size: 1rem;
          color: $primary-color;
          text-decoration: none;
          list-style: none;
          font-weight: 650;
        }
        p {
          margin: 0.5rem 0;
          font-size: 1.25rem;
          line-height: 170%;
        }
        .about_chart img {
          border-radius: 10px;
          margin: 2rem 0;
          object-fit: contain;
          width: 98%;
        }
        .timeline_img img {
          margin: 2rem 0;
          object-fit: contain;
          width: 98%;
        }
      }
    }
  }

  .card_container {
    // height: 410px;

    .quick_link {
      grid-column: 1;
      width: 18vw;
      font-size: 1.2rem;
      position: sticky;
      top: 100px;

      #quick_link {
        color: white;
        background-color: #66be94;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        padding: 1rem 3rem 1rem 3rem;
      }

      .link {
        list-style: none;
        a {
          display: flex;
          justify-content: space-between;
          align-items: center;
          color: black;
          background-color: white;
          padding: 0.8rem 2rem;
          text-decoration: none;
          margin: 7px 0;

          // &:hover {
          // }
        }
        #register {
          color: white;
          background-color: $primary-color;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
        }
      }
    }
  }

  .right_section {
    grid-column: 2;
    padding: 1rem 2.7rem;
    margin: 0 0 0 2rem;
    border-radius: 1.5%;
    background-color: white;
    .content {
      margin: 3rem 0;

      h1 {
        margin: 0;
        padding: 0;
        font-size: 3rem;
        color: $primary-color;
        text-decoration: none;
        list-style: none;
        font-weight: 650;
      }
      p {
        margin: 0.5rem 0;
        font-size: 1.25rem;
        line-height: 170%;
        text-align: justify;
      }
      .about_chart img {
        border-radius: 10px;
        margin: 2rem 0;
        object-fit: contain;
        width: 98%;
      }
      .timeline_img img {
        margin: 2rem 0;
        object-fit: contain;
        width: 98%;
      }
    }
  }
}

//*************************** Sponsors ********************************

.Sponsors {
  @include Flex();
  margin: 5rem 3rem;
  padding: 2rem;
  flex-direction: column;
  background-color: #6576a0;
  border-radius: 10px;
  color: white;
  h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    padding: 0;
    margin: 0 0 2rem 0;
  }

  .card_container {
    @include Flex();
    column-gap: 6rem;

    .card {
      @include Flex();
      flex-direction: column;
      .photo {
        height: 90px;
        width: 90px;
        background-color: #d9d9d9;
        border-radius: 50%;
      }
      p {
        font-size: 1.2rem;
        margin: 0.8rem 0;
      }
    }
  }
}

//*************************** Image Slider ********************************

.image_slider {
  \.swiper {
    width: 100%;
    height: 100%;
  }
  .gradient {
    background: radial-gradient(rgba(0, 0, 0, 0), #000000);
    z-index: 1;
  }

  .img {
    height: 95vh;
    z-index: -1;
    width: 100vw;
    background: radial-gradient(rgba(0, 0, 0, 0), #000000);
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .swiper {
    margin-left: auto;
    margin-right: auto;
  }
}

//*************************** Footer ********************************
.footer {
  padding: 3rem 0;
  background-color: #2d2d2d;
  .footer_container {
    @include Flex();
    font-family: "Poppins", sans-serif;
    padding: 1rem 7rem;
    color: white;

    .border_margin {
      width: 3px;
      height: 200px;
      // border-right: solid 3px #535353;
      background-color: #535353;
    }

    .left_section {
      width: 33%;
      @include Flex();
      justify-content: flex-start;
      // padding-left: 2rem;

      .logo {
        margin: 1rem;
        height: 100px;
        width: 100px;
        background-color: #858585;
        border-radius: 8px;
      }
      .details {
        p {
          margin: 0;
          padding: 0rem;
          font-size: 1.3rem;
          font-weight: 600;
        }
        h2 {
          margin: 0;
          padding: 0rem;
          font-size: 3.8rem;
          font-weight: 600;
          line-height: 4.5rem;
        }
        #conference {
          letter-spacing: 3px;
        }
      }
    }
    .middle_section {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: 33%;
      .site_map {
        margin-left: 3rem;

        h2 {
          // line-height: 0rem;
          margin: 0;
          font-size: 2rem;
          font-weight: 600;
        }
        li {
          margin: 5px;
          list-style: none;
        }
        a {
          // display: block;
          text-decoration: none;
          color: white;
          font-size: 1.2rem;
        }
      }
    }
    .right_section {
      width: 33%;
      font-family: "Plus Jakarta Sans", sans-serif;
      // display: flex;
      color: #bebebe;
      @include Flex();

      .weather_left {
        #location {
          height: 1.2rem;
        }

        .logo {
          transform: scale(1.3);
        }
      }

      .weather_middle {
        margin-left: 1rem;

        h2 {
          font-size: 3rem;
          line-height: 0;
          margin: 2rem 0;
          // background: -webkit-linear-gradient(#ffffff, rgba(255, 255, 255, 0));
          // -webkit-background-clip: text;
          // -webkit-text-fill-color: transparent;
        }
      }

      .weather_right {
        padding-left: 1rem;
        font-size: 0.8rem;
        display: flex;
        flex-direction: column;
        row-gap: 1rem;

        .icon {
          margin-right: 1rem;
          width: 20px;
        }
      }
    }
  }
}
